export default {
  computed: {
    lists() {
      return this.$store.state.lists
    }
  },
  created() {
    if (!this.lists) {
      this.$store.dispatch('getLists')
    }
  },
  methods: {
    setDefault(list, lists) {
      lists.forEach((item) => {
        item.isDefault = false
      })
      list.isDefault = true
    },
    toEdit(list) {
      this.$router.push({
        name: 'form',
        query: {
          type: 'edit',
          instance: list
        }
      })
    }
  }
}



