
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'
import value2name from 'vux/src/filters/value2name.js'


export default {
props: [ 'ids' ],
components: {
Group,
XAddress,
XButton
},
data () {
return {
title: '',
addressData: ChinaAddressV4Data,
showAddress: false,
value: JSON.parse(JSON.stringify(this.ids))
}
},
methods: {
doShowAddress () {
this.showAddress = true
setTimeout(() => {
this.showAddress = false
}, 2000)
},
onShadowChange (ids, names) {
console.log('change', ids, names)
this.$emit('sendAddressValue', ids, names)
},
changeData () {
this.value2 = ['430000', '430400', '430407']
},
changeDataByLabels () {
this.value2 = ['', '', '']
},
changeDataByLabels2 () {
this.value2 = ['', '', '--']
},
getName (value) {
return value2name(value, ChinaAddressV4Data)
},
logHide (str) {
console.log('on-hide', str)
},
logShow (str) {
console.log('on-show')
},
sendAddressValue() {
console.log('send', this.value)
}
}
}



